/* google font  */
@import url('https://fonts.googleapis.com/css?family=Poppins:200,200i,300,300i,400,400i,500,500i,600,600i,700,700i');

@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap');

/* navbar  */

.navbar {
  background: rgba(0, 0, 0, 0.6);    
}

.navbar-toggler {
  border: none!important;  
  outline: none!important;
}

.navbar-toggler i {
  color: white;
}

.navbar-nav .nav-item .nav-link{
  border: none!important;  
  outline: none!important;
  color: white!important;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
}

.navbar-nav .nav-item .nav-link:hover{
  
}

.brand .navbar-brand {
  font-size: 25px;
  font-family: 'Roboto', sans-serif;
  color: white!important;  
}

.brand .navbar-brand span {
  color: white;
  font-size: 32px;
}

.nav-link::after {
  content: '';
  display: block;
  height: 2px;
  width: 0;
  background: #0ba376;
  transition: width .3s;
}

.nav-link:hover::after {
  width: 5%;
  transition: width .3s;
}

/* for all */
.main {
  height: 100vh;
  width: 100%;
}

/* home */
.main1 {
  background-image: url('../img/workhard.jpg');
  background-size: cover;
}

.main1::after {
  content: '';
  display: block;
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  position: absolute;
  bottom: 0;
}

.main1 img {
  margin-top: 150px;
  width: 150px;
  height: 150px;
  border-radius: 100%;
  position: relative;
  z-index: 1;
  border: 4px solid white;
}

.main1 .container {
  text-align: center;
  color: white;
  text-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  z-index: 1;
  position: relative;

}

.main1 .display-4 {
  margin-top: 40px;
  font-weight: 400;
  font-size: 30px;
  font-family: 'Poppins', Helvetica, sans-serif;
}

.main1 .container span {
  font-weight: 500;
}

.main1 .container .lead {
  font-size: 20px;
  font-family: 'Poppins', Helvetica, sans-serif;
}
/* close home  */


/* main2 utilities */
.main2 {
  background-image: url('../img/bg-img3.jpg');
  background-size: cover;
}

/* about */
.main2 .about {
  margin-top: 120px;
}

.main2 .about img {
  width: 200px;
  height: 290px;
  border-radius: 10px;
  position: relative;
}
.main2 .about .custom {
  position: absolute;
  margin-left: -190px;
  margin-top: 10px;
}

.about p {
  font-family: 'Poppins', Helvetica, sans-serif;
  font-size: 15px;
}

.about span {
  color: #0ba376;
}

.about2 .drawer {
  display: table;
  width: 100%;
  background-color:white;
  padding: 25px;
  border-radius: 10px;
}

.about2 .drawer img {
  display: block;
  max-width: 100%;
}

.about2 h4{
  font-family: 'Poppins', Helvetica, sans-serif ;
}

.about2 p {
  font-family: 'Poppins', Helvetica, sans-serif ;
  font-size: 15px;
}

.about2 span {
  color: #0ba376;
}

/* resume */
.main2 .resume {
  margin-top: 120px;
}

.main2 .resume i {  
  font-size: 20px;
  color: #0ba376;
}

.main2 .resume h4 {
  font-family: 'Poppins', Helvetica, sans-serif;
}

.main .resume p {
  font-family: 'Poppins', Helvetica, sans-serif;
  font-size: 15px;
}

.main2 .contact  {
  margin-top: 150px;
}

.carousel-inner .carousel-item img {
  border-radius: 10px;
}
.gallery a {
  color: #0ba376!important;
  text-decoration: none!important;
}

.gallery a:hover {
  color: black!important;
}

/* close resume */

.main-blank {
  background-image: url('../img/home-banner.jpg');
  background-size: cover;
}

.main-blank .text-center {
  margin-top: 200px;
}

/* --- Social Links --- */
.social-media {
  margin: 40px 0;
}

.social-media ul {
  list-style: none;
  padding: 0;
}

.social-media ul li {
  display: inline-block;
  margin: 0 5px;
}

.social-media ul li a {
  color: #fff;
  border-radius: 20px;
  font-size: 18px;
  padding: 0;
  height: 30px;
  width: 30px;
  display: block;
  line-height: 30px;
  text-align: center;
  opacity: .9;
}

.social-media ul li a:hover {
  opacity: 1;
  background-color: rgba(255,255,255,.2);
}
/* --- End Social Links --- */

.contact i {
  font-size: 33px;
  color: #0ba376;
  
}

.contact h4 {
  font-family: 'Poppins', Helvetica, sans-serif;
  font-size: 15px;
  margin-top: 8px;
  font-weight: 600;
}

.map {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  margin: 0 0 35px;
}


/* dekstop */
@media (min-width: 992px) {

  .navbar {
    background: transparent;
  }
  .brand .navbar-brand {
    font-size: 32px;
    border: none!important;  
    outline: none!important;
    font-family: 'Roboto', sans-serif; 
    color: black!important;
  }

  .navbar-nav .nav-item .nav-link{  
    color: black!important;    
  }
  .nav-link {
    margin-left: 30px;
    text-transform: uppercase;
    font-weight: 500;
     
  }
  .nav-link::after {
    content: '';
    display: block;
    height: 2px;
    width: 0;    
    transition: width .3s;
  }

  .nav-link:hover::after {
    width: 50%;
    transition: width .3s;
  }
  .main1 img {
    margin-top: 100px;
    width: 250px;
    height: 250px;
    border-radius: 100%;
    position: relative;
    z-index: 1;
  }
  .main1 .display-4 {
    font-weight: 300;
    font-size: 50px;
    font-family: 'Poppins', Helvetica, sans-serif;
  }
  .main1 .lead {
    font-weight: 300;
    font-size: 30px;
    font-family: 'Poppins', Helvetica, sans-serif;
  }
  .main1 .container span {
    font-weight: 500;
    font-family: 'Poppins', Helvetica, sans-serif;
  }

  .about2 .drawer {
    display: table;
    width: 120px;
    height: 100%;
    background-color:white;
    padding: 25px;
    border-radius: 10px;
  }
  
  

  
  



}

